import { getWeb3 } from '../../v3/utils/web3';
import { DEBUG } from '../config';
import { AppError } from '../error/error';
import { web3Factory } from '../factory/web3';
import { numberToHex } from '../utils/convert';

const MAX_GAS_AMOUNT = 832731 * 3;
// const RE_ERROR_MSG = /\"message\":\s\"execution\sreverted:([\w\s]+)\"/
const RE_ERROR_MSG = /(\{[\s\S]+\})/

const noOp = () => {}

export class ContractBase {
  constructor(chainId, contractAddress, contractAbi) {
    this.chainId = chainId;
    this.contractAddress = contractAddress;
    this.contractAbi = contractAbi;
  }

  async _init() {
    // re-init web3 and contract when web3 instance is null
    if (!this.web3) {
      if (!!this.useProvider) {
        this.web3 = await getWeb3(this.chainId);
      } else {
        this.web3 = await web3Factory.get(this.chainId);
      }
      // this.web3.eth.handleRevert = true
      this.contract = new this.web3.eth.Contract(
        this.contractAbi,
        this.contractAddress
      );
    }
  }

  async _call(method, args = []) {
    let res;
    let retry = 3;
    // clear web3 cache in brower env
    if (this.web3 && !this.useProvider && window) {
      this.web3 = null;
    }
    while (retry > 0) {
      try {
        await this._init();
        res = await this.contract.methods[method](...args).call();
        return res;
      } catch (err) {
        DEBUG && console.log(err);
        // remove web3 instance to re-init
        if (retry === 1) {
          this.web3 = null;
        }
        // if (err.toString().includes('Invalid JSON RPC response')) {
        //   console.log(`Invalid JSON RPC response with chainId(${this.chainId})`);
        // } else if (err.toString().includes("Returned values aren't valid,")) {
        //   console.log(`Invalid contract address(${this.contractAddress}) and chainId(${this.chainId})`);
        // } else {
        //   //console.log('error:', err.toString())
        // }
      }
      retry -= 1;
    }
    throw new AppError("JSON_RPC_CALL_TIMEOUT", {
      name: method,
      args: [...args, this.contractAddress],
    });
  }

  async _estimatedGas(method, args = [], accountAddress, opts, onWarning) {
    await this._init();
    let gas = 0;
    for (let i = 0; i < 2; i++) {
      try {
        gas = await this.contract.methods[method](...args).estimateGas({
          from: accountAddress,
          ...opts,
        });
        gas = parseInt(gas * 1.25);
        break;
      } catch (error) {
        // console.log(`-- estimatedGas: ${error}`)
        const res = error.toString().match(RE_ERROR_MSG);
        if (Array.isArray(res) && res.length >= 2) {
          const errorObject = JSON.parse(res[1]);
          const msg =
            (errorObject.message &&
              errorObject.message.replace("execution reverted: ", "")) ||
            "";
          onWarning(msg);
        }
      }
    }
    if (gas === 0) gas = MAX_GAS_AMOUNT;
    return gas;
  }

  // _getTransactionReceipt(tx) {
  //   const self = this;
  //   return function _transactionReceipt(resolve, reject) {
  //     self.web3.eth.getTransactionReceipt(tx, (error, receipt) => {
  //       if (error) {
  //         reject(error);
  //       } else if (receipt === null) {
  //         setTimeout(() => _transactionReceipt(resolve, reject), 500);
  //       } else if (receipt.status === false) {
  //         //receipt.errorMessage = 'Transaction failed';
  //         reject(receipt);
  //       } else {
  //         resolve(receipt);
  //       }
  //     });
  //   };
  // }
  async _transact(method, args = [], accountAddress, opts = {}) {
    // clean web3 instance
    this.web3 = null;
    await this._init();
    let restOpts = {};
    let { onAccept, onReject, onWarning, onReceipt, value, nonce } = opts;

    // set default value if callback is empty
    if (!onAccept) {
      onAccept = noOp;
    }
    if (!onReject) {
      onReject = noOp;
    }
    if (!onWarning) {
      onWarning = noOp;
    }
    if (!onReceipt) {
      onReceipt = noOp;
    }

    // for ETH/BSC transfer
    if (value) {
      restOpts.value = value;
    }
    if (nonce) {
      restOpts.nonce = nonce;
    }

    let [gas, gasPrice] = await Promise.all([
      this._estimatedGas(method, args, accountAddress, restOpts, onWarning),
      this.web3.eth.getGasPrice(),
    ]);

    let res;
    try {
      res = await this.contract.methods[method](...args)
        .send({
          from: accountAddress,
          gas,
          gasPrice,
          ...restOpts,
        })
        .on("transactionHash", (txHash) => {
          onAccept();
          // }).on('receipt', () => {
          //   onReceipt()
        })
        .on("error", (error) => {
          console.error(error)
          if (error.code === 4001) {
            // user reject
            onReject();
          }
        });
    } catch (err) {
      throw err
    } finally {
      //  close the dialog
      if (onReceipt && typeof onReceipt === "function") {
        onReceipt();
      }
    }
    return res;
  }
}
