import './welcome.less'
import deri from './img/deri.svg'
import arbitrum from './img/arbitrum.svg'
import { hide } from 'react-functional-modal'
import { setCookie } from '../../utils/utils'
export default function Welcome() {
  const hideWelcome = () => {
    // setCookie('welcome-nft', 'false')
    hide('Welcome')
  }
  return (
    <>
      <div className='welcome-arbitrum'>
        <div className='welcome-box'>
          <div className='welcome-logo'>
            <div className='deri-logo'>
              <img src={deri} alt="deri" />
            </div>
            <div className='arbitrum-logo'>
              <img src={arbitrum} alt="arbitrum" />
            </div>
          </div>
          <div className='welcome-title'>
            <div className='title-one'>
              Deri Festival on
            </div>
            <div className='title-two'>
              ARBITRUM
            </div>
          </div>
          <div className='welcome-info'>
            <div className='welcome-info-title'>
              Event Period
            </div>
            <ul>
              <li>
                08:00 AM UTC, July 11 to 07:59 AM UTC July 18, 2022
              </li>
            </ul>
          </div>
          <div className='welcome-info'>
            <div className='welcome-info-title'>
              Eligible users
            </div>
            <ul>
              <li>
                All users claimed the Arbitrum’s Odyssey bridge week campaign NFT
              </li>
            </ul>
          </div>
          <div className='welcome-info'>
            <div className='welcome-info-title'>
              Tasks
            </div>
            <ul>
              <li>
                Providing liquidity ≥ $10 to Main Pool
              </li>
              <li>
                Derivatives trading volume ≥ $10
              </li>
            </ul>
          </div>
          <div className='learn-more-btn'>
            <a onClick={() => { window.open("https://deri-protocol.medium.com/deri-festival-on-arbitrum-aad2c8479bb6") }} target="_blank">LEARN MORE</a>
            <div className='btn-ok' onClick={hideWelcome}>
              OK
            </div>
          </div>
        </div>
      </div>
    </>
  )
}